@import "./variables";

* {
    box-sizing: border-box;
}

body {
	font-family: $font;
	background: $primary;
    height: 100vh;
}

#root {
	height: 100%;
	display: flex;
    justify-content: center;
}

.container {
	text-align: center;
	color: #fff;
	margin-top: 100px;

	> header {
		font-size: 1.3rem;
		font-weight: 700;
		margin: 0 auto 60px auto;
		position: relative;
		height: 30px;
		width: 250px;
        overflow: hidden;
    }
}
	
.header-headings {
	display: flex;
	flex-direction: column;
	position: absolute;
	width: 100%;
	transition: all .4s cubic-bezier(.785,.135,.15,.86);

	> span {
		margin: 3px 0;
    }
	&.login {
		transform: translateY(0);
    }
	&.register {
        transform: translateY(-35px);
    }
}
	
.options {
	display: flex;
	align-items: center;
    width: 350px;
    list-style-type: none;
    padding-left: 0;

	@media screen and (max-width: 380px) {
        width: 100%;
    }

	> li {
		cursor: pointer;
		opacity: .5;
		transition: all .2s ease;
		
		&:hover {
			opacity: 1;
        }
		&.active {
			opacity: 1;
        }
		&:nth-of-type(2) {
			margin-left: 15px;
        }
    }
}
	
.account-form {
	display: flex;
	flex-direction: column;
    margin-top: 30px;
}

.account-form-fields {
	display: flex;
	flex-direction: column;
	overflow: hidden;
	transition: all .4s cubic-bezier(.785,.135,.15,.86);

	&.login {
		max-height: 116px;
    }
	&.register {
		max-height: 250px;
    }
	> input {
		border: 0;
		margin-bottom: 10px;
		padding: 15px;
		font-size: 1rem;
		font-family: Nunito, sans-serif;
		color: #000;
		border-radius: 5px;
	
		&::placeholder {
			color: #aaa;
        }
		&::-webkit-input-placeholder {
			color: #aaa;
        }
		&:-ms-input-placeholder {
			color: #aaa;
        }
		&:focus {
            outline: none;
        }
    }
}
	
.btn-submit-form {
	border: 0;
	appearance: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	cursor: pointer;
	padding: 15px 0;
	border-radius: 5px;
	color: #fff;
	font-size: 1rem;
	font-family: Nunito, sans-serif;
	background: lighten($primary, 5%);
	box-shadow: 0px 1px 2px rgba(0,0,0,.13);
	transition: all .2s ease;

	&:hover {
		background: darken($primary, 2%);
    }
	&:active, &:focus {
		outline: none;
        background: darken($primary, 7%);
    }
}